// "Arial", sans-serif;
@font-face {
  font-family: "Roboto Condensed";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src:
    url("../fonts/roboto-condensed-v19-latin_cyrillic-regular.woff2") format("woff2"),
    url("../fonts/roboto-condensed-v19-latin_cyrillic-regular.woff") format("woff");
}

@font-face {
  font-family: "Roboto Condensed";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src:
    url("../fonts/roboto-condensed-v19-latin_cyrillic-700.woff2") format("woff2"),
    url("../fonts/roboto-condensed-v19-latin_cyrillic-700.woff") format("woff");
}

//"Courier New", monospace
@font-face {
  font-family: "PT Mono";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src:
    url("../fonts/pt-mono-v8-latin_cyrillic-regular.woff2") format("woff2"),
    url("../fonts/pt-mono-v8-latin_cyrillic-regular.woff") format("woff");
}

@font-face {
  font-family: "Roboto";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src:
    url("../fonts/roboto-v20-cyrillic-500.woff2") format("woff2"),
    url("../fonts/roboto-v20-cyrillic-500.woff") format("woff");
}
